#animate_figure {
    position: fixed;
    z-index: -1;
    max-width: 1440px;
    margin-left: 50%;
    transform: translateX(-50%);
    width: 100%;
    height: 100%;
}

.animate_figure-white {
    background: url(/assets/icon/cfiS3.png) no-repeat center;
    display: block;
    position: absolute;
    width: 97px;
    height: 110px;
    z-index: -1;
    top: 5%;
    left: 4%;
}

.animate_figure-blue {
    background: url(/assets/icon/cfiS2.png) no-repeat center;
    display: block;
    position: absolute;
    width: 153px;
    height: 130px;
    top: 14%;
    left: 74%;
    z-index: -1;

}

.animate_figure-whiteBig {
    position: absolute;
    display: block;
    background: url(/assets/icon/cfiS1.png) no-repeat center;
    width: 221px;
    height: 416px;
    top: 44%;
    left: 36%;
    z-index: -1;
}

@keyframes zoomIn {
    from {
        opacity: 0;
        transform: scale(0.6);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

[data-animate] {
    opacity: 0;
    transform: scale(0.5);
}

[data-animate].visible {
    opacity: 1;
    transform: scale(1);
    animation: zoomIn 0.6s ease-in-out;
}

@keyframes animate0-white {

    to {
        top: 5%;
        left: 4%;
    }

    from {
        top: 36%;
        left: 95%;
    }
}

@keyframes animate0-whiteBig {
    to {
        top: 44%;
        left: 36%;
    }

    from {
        top: -7%;
        left: 18%;
    }
}

@keyframes animate0-blue {
    to {
        top: 14%;
        left: 74%;
    }

    from {
        top: 82%;
        left: 23%;
    }
}

@keyframes animate1-white {
    0% {
        top: 5%;
        left: 4%;
    }

    50% {
        top: 45%;
        left: 70%;
    }

    100% {
        top: 36%;
        left: 95%;
    }
}

@keyframes animate1-whiteBig {
    0% {
        top: 44%;
        left: 36%;
    }

    50% {
        top: 20%;
        left: 22%;
    }

    100% {
        top: -7%;
        left: 18%;
    }
}

@keyframes animate1-blue {
    to {
        top: 82%;
        left: 23%;
    }
}


@keyframes animate2-white {
    0% {
        top: 36%;
        left: 95%;
    }

    20% {
        top: 10%;
        left: 60%;
    }

    50% {
        top: -13%;
        left: 51%;
    }

    100% {
        left: 0;
        top: -100%;
    }
}

@keyframes animate2-whiteBig {
    0% {
        top: -7%;
        left: 18%;
    }

    20% {
        top: -10%;
        left: 30%;
    }

    50% {
        top: 35%;
        left: 55%;
    }

    100% {
        top: 30%;
        left: 2000%;
    }
}

@keyframes animate2-blue {
    0% {
        display: none;
    }

    100% {
        display: none;
    }

}

@keyframes animate3-white {
    0% {
        top: 0;
        left: 0;
    }

    50% {
        top: 40%;
        left: 65%;
    }

    100% {
        top: 20%;
        left: 95%;
    }
}

@keyframes animate3-whiteBig {
    0% {
        top: 40%;
        left: 100%;
    }

    50% {
        top: 35%;
        left: 45%;
    }

    100% {
        top: 30%;
        left: 18%;
    }
}

@keyframes animate3-blue {
    0% {
        top: 0;
        left: 0;
    }

    50% {
        top: 40%;
        left: 65%;
    }

    100% {
        top: 90%;
        left: 92%;
        ;
    }
}

@keyframes animate4-white {
    0% {
        top: 20%;
        left: 95%;
    }

    50% {
        top: 15%;
        left: 50%;
    }

    70% {
        top: 65%;
        left: 21%;
    }

    100% {
        top: 80%;
        left: 25%;
    }
}

@keyframes animate4-whiteBig {
    0% {
        top: 30%;
        left: 20%;
    }

    50% {
        top: 45%;
        left: 80%;
    }

    100% {
        top: 20%;
        left: 92%;
    }
}

@keyframes animate4-blue {
    0% {
        top: 90%;
        left: 92%;
        ;
    }

    50% {
        top: 47%;
        left: 51%;
    }

    100% {
