@keyframes animate5-white {
    0% {
        top: 80%;
        left: 25%;
    }

    50% {
        top: 61%;
        left: 3%;
    }

    90% {
        top: 49%;
        left: -7%;
    }

    100% {
        top: 49%;
        left: -7%;
        display: none;
    }
}

@keyframes animate5-whiteBig {
    0% {
        top: 20%;
        left: 92%;
    }

    50% {
        top: 47%;
        left: 67%;
    }

    90% {
        top: 100%;
        left: 74%;
    }

    100% {
        top: 100%;
        left: 74%;
        display: none;
    }
}

@keyframes animate5-blue {
    0% {
        top: 37%;
        left: 21%;
    }

    20% {
        top: 22%;
        left: 42%;
    }

    50% {
        top: 30%;
        left: 86%;
    }

    90% {
        top: 39%;
        left: 100%;
    }

    100% {
        top: 39%;
        left: 100%;
        display: none;
    }
}

.animate_0>.animate_figure-white {
    animation: 1.3s animate0-white cubic-bezier(0, 0, 1, 0.99) forwards;
}

.animate_0>.animate_figure-blue {
