}

.animate_gif-1, .animate_gif-2, .animate_gif-3, .animate_gif-4 {
    display: block;
    position: relative;
    width: 12px;
    height: 12px;
    border-radius: 100%;
    transition: .3s linear;
    opacity: 0;
}

@keyframes moveRight {
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    80% {
        transform: translateX(415px);
        opacity: 1;
    }
    90% {
        transform: translateX(415px);
        opacity: 1;
    }
    95% {
        transform: translateX(415px);
        opacity: 1;
    }
    100% {
        transform: translateX(415px);
        opacity: 0;
    }
}
@keyframes trail {
    0% {
        opacity: 0.5;
        width: 0;
    }
    50% {
        opacity: 0.5;
        width: 140px;
    }
    100% {
        opacity: 0;
        width: 0;
    }
}
.animate_gif-1 {
    background: #2b84f4;
    top: 5px;
    animation: moveRight 3s ease-out infinite;
}
.animate_gif-1:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    background: linear-gradient(90deg, rgba(238,238,241,1) 6%, rgba(189,219,250,1) 29%, rgba(0,133,255,1) 66%, rgba(0,133,255,1) 100%);
    filter: blur(1px);
    transform: translate(-96%, -50%);
    animation: trail 3s linear infinite;
    z-index: -1;
    width: 0;
    height: 12px;
}
.animate_gif-2 {
    background: #E6E7E9;
    animation: moveRight 3s ease-out infinite;
    animation-delay: .8s ;
    top: 17px;
}
.animate_gif-2:after {
