.header {
    background-color:  #F6F5F8;
    display: grid;
    align-items: center;
    grid-template-columns: 85px auto auto;
    height: 72px;
    padding: 12px 0 20px 0;
    border-bottom: 2px solid var(--main_color-black);
}
.header_nav_burger {
    display: none;
    width: 40px;
    height: 40px;
    padding: 14px 11px;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.header_nav_burger_line-1, .header_nav_burger_line-2, .header_nav_burger_line-3 {
    width: 100%;
    height: 1px;
    border: 1px solid #000F1D;
}

.header_logo {
    position: relative;
    top: 3px;
    left: -3px;
}
.header_nav {
    position: relative;
    font-size: 1.6em;
    margin-left: 46px;
}
.header_nav:before, .header_nav:after {
    content: '';
    position: absolute;
    display: block;
    border: 1px solid var(--main_color-black);
    width: 25px;
    transform: rotate(-45deg);
    bottom: -21px;
}
.header_nav:before {
    left: -45px;
}
.header_nav:after {
    right: -15px;
}
.header_nav > div > ul {
    list-style: none;
    display: flex;
    align-items: center;
    position: relative;
    top: 1px;
    left: 1px;
}

.header_nav > div > ul > li {
    /*margin-left: 32px;*/
    margin-left: 2em;
}
.header_nav > div > ul > li:first-child {
    margin-left: 0;
}
.header_nav > div > ul > li > a {
    color: var(--main_color-black);
    text-decoration: none;
}
.header_platform, .header_nav_platform {
    display: grid;
    grid-template-columns: 165px 207px;
    align-items: center;
    justify-content: end;
    column-gap: 19px;
    /*column-gap: 1.5em;*/
}
.header_platform > div > a, .header_nav_platform > div > a {
    display: block;
    text-decoration: none;
}
.header_platform_addP {
    position: relative;
    left: -3px;
}

.header_platform_feedback:hover a, .header_platform_feedback:active a{
    color: var(--main_color-white);
}
.header_nav_platform {
    display: none;
}

