.howWorks_section {
    padding-top: 120px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.howWorks_section_content {
    max-width: 864px;
    width: 100%;
}
.howWorks_section_content_slide {
    display: flex;
    background:  transparent;
}
.howWorks_section_content_slide > p {
    text-align: left;
    margin-left: 32px;
    line-height: 24px;
}
