.why_cfi {
    display: grid;
    align-items: flex-start;
    justify-content: space-between;
    grid-template-columns: repeat(2,auto);
}
.why_cfi_content {
    max-width: 864px;
    width: 100%;
}

.why_cfi_content_table-textLeft {
    text-align: left;
}
.why_cfi_content_table-text {
    display: flex;
    align-items: center;
    margin-top: -9px;
}
.why_cfi_content_table_wrapper {
    display: grid;
    grid-template-columns: 284px auto auto;
    align-items: center;
    justify-content: space-between;

}
.why_cfi_numeric {
    height: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
}
.why_cfi_content_table {
    margin-left: -3px;
    margin-top: -4px;
}
.why_cfi_content_table-text-line {
    position: relative;
    left: 4px;
    top: -4px;
    display: block;
    width: 38px;
    height: 1px;
    transform: rotate(-62deg);
    border: 1px solid #000F1D;
    margin-right: 21px;
}
