.cfi_section > h1 {
    display: flex;
    flex-direction: column;
    letter-spacing: -2px;
}
.cfi_section_title, .cfi_section_subtitle-text {
    text-transform: uppercase;
}
.cfi_section_subtitle {
    margin-top: 16px;
    display: flex;
    align-items: center;
}
.cfi_section_subtitle > .animate_gif {
    /*height: 90px;*/
    height: 6em;
    max-width: 415px;
    width: 100%;
    margin-right: 33px;
}
.cfi_section-img {
    overflow: hidden;
}
.cfi_section-img > img {
    max-width: 1312px;
    min-width: 591px;
    width: 100%;
    position: relative;
    /*left: -3px;*/
}

.cfi_section-ps {
    position: relative;
    left: -13px;
    margin-top: 15px;
    margin-bottom: 64px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-left: -5.1em;
    max-width: 81em;
    width: 100%;
    line-height: 24px;
    /*font-size: 1.6em;*/
    font-size: 16px;
    font-weight: 400;
}
.cfi_section_title {
    position: relative;
}
