.header_fullScreen_menu {
    display: block !important;
    left: 0;
    top: 0;
    z-index: 1;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: var(--main_color-white);
}
.header_fullScreen_menu_head-exit {
    display: flex;
    align-items: center;
    justify-content: center;
    float: right;
    margin-right: 20px;
    margin-top: 12px;
    width: 40px;
    height: 40px;
    padding: 14px;
    position: relative;
}
.header_fullScreen_menu_head-exit:before, .header_fullScreen_menu_head-exit:after {
    content: '';
    width: 12px;
    border: 1px solid #000F1D;
    position: absolute;
    display: block;
}
.header_fullScreen_menu_head-exit:before {
    transform: rotate(230deg);
}
.header_fullScreen_menu_head-exit:after {
    transform: rotate(-230deg);
}

.header_fullScreen_menu > ul {
    height: 100%;
    flex-direction: column;
    align-items: flex-start !important;
    padding: 39px 64px;
}
.header_fullScreen_menu > ul > li {
    margin-left: 0 !important;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 87px;
    width: 100%;
    border-bottom: 1px solid #E6E7E9
}
.header_fullScreen_menu > ul > li > a {
    font-size: 16px;
}
.header_fullScreen_menu_head {
    display: none;
}
.header_fullScreen_menu .header_fullScreen_menu_head {
    display: block;
    width: 100%;
    height: 52px;
}
.header_fullScreen_menu_numeric {
    display: none !important;
}
.header_fullScreen_menu .header_fullScreen_menu_numeric {
    display: block !important;
}
.header_fullScreen_menu .flex-numeric {
    margin-bottom: 0;
}
