.btn-mini {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    padding: 8px 12px;
    font-size: 16px;
}
.btn-big {
    text-align: center;
    border-radius: 16px;
    padding: 53px 40px 50px 32px;
    max-width: 190px;
    line-height: 24px;
    width: 100%;
    height: 148px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--main_color-white);
    cursor: pointer;
}
.btn-big_title {
    display: block;
}
.btn-mini-white {
    color: var(--main_color-black);
    transition: .2s linear;
    background: var(--main_color-white);
}
.btn-mini-blue {
    background: var(--main_color-blue);
    transition: .2s linear;
}
.btn-mini-white:hover, .btn-mini-blue:hover {
    color: white;
    background: #339DFF;
}
.btn-mini-white:active, .btn-mini-blue:active {
    color: white;
    background: #1272CC;
}
.color-black {
    color: var(--main_color-black);
}
.color-white {
    color: var(--main_color-white);
}
.mt-section-120 {
    margin-top: 120px;
}
.home-title {
    font-weight: 500;
    font-size: 76px;
    /*font-size: 5.1em;*/
}
.flex-numeric {
    display: flex;
    align-items: center;
}
.numeric-section {
    width: 54px;
    height: 54px;
    border: 2px solid #E6E7E9;
    border-radius: 100%;
    padding: 14px 16px 14px 16px;
    margin-right: 24px;
}
.title-section {
    position: relative;
    font-weight: 500;
    line-height: 46px;
    /*font-size: 4.2em;*/
    font-size: 42px;
}
.little-title-section {
    font-size: 20px;
    /*font-size: 2em;*/
    line-height: 28px;
}
.little-text {
    font-size: 16px;
    /*font-size: 1.6em;*/
}
.borderB-white {
    border: 1px solid var(--main_color-white);

}
.borderB-black {
    border: 1px solid #000F1D;
}
.border-section {
    margin: 64px 0;
    width: 100%;
    height: 1px;
}
