.product-cart {
    border-radius: 16px;
    padding: 24px;
    display: flex;
    align-items: flex-end;
}
.m-center {
    margin: 0 auto;
}

.text-right {
    text-align: right;
}
.paragraph-section-text {
    padding-left: 2.7em;
    position: relative;
    display: block;
}
.client-cart {
    background: white;
    display: flex;
    align-items: baseline;
    justify-content: inherit;
    flex-direction: column;
    width: 100%;
    height: 326px;
    padding: 23px;
    border-radius: 16px;
    border: 1px solid #E6E7E9;
}

.main-line {
    position: relative;
    left: 4px;
    top: -4px;
    display: block;
    width: 32px;
    height: 1px;
    transform: rotate(-62deg);
    border: 1px solid #000F1D;
    margin-right: 21px;
}
.main-line_white {
    border: 1px solid var(--main_color-white);
}
.max-width-none {
    max-width: none;
}

.blue-section {
    background: #0085FF;
    border-radius: 30px;
}
.border-section-mini {
    margin: 48px 0 44px 0;
}
.border-section-middle {
    margin: 64px 0 48px 0;
}
.hover-text {
    transition: .2s linear;
}
.hover-text:hover {
    color: #339DFF;
}
.hover-text:active {
    color: #1272CC;
}
.blue-text {
    color: var(--main_color-blue);
}
