* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'MyInter', serif;
}
body {
    background-color: #F6F5F8;
    font-size: 10px;
    overflow-x: hidden;
    scroll-behavior: smooth;
}
.container {
    max-width: 1440px;
    width: 100%;
    padding-left: 64px;
    padding-right: 64px;
    margin: 0 auto;
}
