.footer {
    padding: 120px 0;
    position: relative;
    z-index: 2222;
    display: grid;
    align-items: start;
    justify-content: space-between;
    grid-template-columns: repeat(2, auto);
}
.footer_numeric {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    height: 86%;
}
.footer:before {
    content: '';
    display: block;
    position: absolute;
    background: url("/assets/img/footer1.png") no-repeat center center;
    width: 472px;
    height: 920px;
    top: 47px;
    left: calc(21% - -8px);
    z-index: -1;
}
.footer:after {
    content: '';
    display: block;
    position: absolute;
    background: url("/assets/img/footer2.png") no-repeat center center;
    width: 261px;
    height: 222px;
    right: 17%;
    top: 45px;
    z-index: -1;
}
.footer_wrapper {

}
.footer_wrapper_content {
    max-width: 864px;
    width: 100%;
    background: var(--main_color-white);
    padding: 56px 56px 50px 56px;
    border-radius: 16px;
}
.footer_wrapper_content_subtitle {
    display: grid;
    grid-template-columns: 271px auto;
    align-items: center;
}
.footer_wrapper_content_ul_li {
    display: flex;
}
.footer_wrapper_content_ul_li-img {
    margin-right: 7px;
}
.footer_wrapper_content_page_up {
    margin-top: 83px;
}
.footer_wrapper_content_feedback {
    display: flex;
    flex-direction: column;
    margin-left: 50px;
}
.footer_wrapper_content_feedback_input > input {
    outline: none;
    padding: 12px 10px;
    border: none;
    width: 100%;
    color: #8C9399;
    font-size: 16px;
    font-weight: 400;
    border-bottom: 1px solid var(--main_color-black);
}
.footer_wrapper_content_feedback_input > input:focus {
    border-bottom: 1px solid #339DFF
;
}
.footer_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.footer_wrapper_content_feedback_wrapper {
    margin-top: 16px;
    position: relative;
    margin-left: -5px;
    left: 5px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.footer_wrapper_content_feedback_wrapper > label {
    margin-right: 8px;
}
.footer_wrapper_content_feedback_wrapper p {
    font-size: 13px;
    line-height: 24px;
    font-weight: 400;
}
.footer_wrapper_content_feedback_wrapper a {
    text-decoration: none;
    font-weight: 500;
}
.footer_wrapper_content_feedback_wrapper > label > input {
    width: 20px;
    height: 24px;
}
.footer_develop {
    background: var(--main_color-white);
    border-radius: 16px;
    padding: 16px 32px;
}
.footer_develop_wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer_develop_wrapper_policy a , .footer_develop_wrapper_rnd {
    cursor: pointer;
    text-decoration: none;
    font-size: 14px;
    color: var(--main_color-black);
}
.footer_develop_wrapper_policy a {
    border-bottom: 1px solid var(--main_color-black);
}
.footer_develop_wrapper_policy, .footer_develop_wrapper_rnd {
    display: flex;
    justify-content: space-between;
}
.footer_develop_wrapper_logoText {
    font-size: 13px;
}
.footer_develop_wrapper_policy_cookie {
    margin-left: 24px;
}
.footer_develop_wrapper_rnd-text {
    margin-right: 8px;
}
.footer_wrapper_content_line {
    position: relative;
}
.footer_wrapper_content_line:before {
    content: '';
    display: block;
    background: url("/assets/icon/lineFooter.png") no-repeat center center;
    background-size: contain;
    width: 100%;
    height: 10px;
    right: 0;
    position: absolute;
    bottom: -19px;
}
