.advantages_section {
    display: grid;
    align-items: start;
    justify-content: space-between;
    grid-template-columns: repeat(2, auto);
}
.advantages_section_numeric {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    flex-direction: column;
    height: 100%;
}
.advantages_section_card-title {
    display: flex;
    align-items: baseline;
}
.advantages_section_card {
    margin-top: 50px;
    max-width: 864px;
    width: 100%;
}
.advantages_section_card_content-two {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}
.advantages_section_card_content-two > .advantages_section_card_content_item {
    /*height: 241px;*/
}
.advantages_section_card_content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.advantages_section_card_content_item {
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    max-width: 288px;
    justify-content: space-between;
    background: var(--main_color-white);
    padding: 24px;
}
.advantages_section_card_content_item:first-child {
    border-top-left-radius:16px;
    border-bottom-left-radius:16px;
    border-right: 1px solid #E6E7E9;

}
.advantages_section_card_content_item:last-child {
    border-top-right-radius:16px;
    border-bottom-right-radius:16px;
    border-left: 1px solid #E6E7E9;
}
.advantages_section_card_content_item-title {
    margin-bottom: 77px;
}
.advantages_section_card_content_item > div > span {
    font-size: 68px;
    font-weight: 500;
    position: relative;
    top: 0;
}
.advantages_section_card_content_item-text {
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}
.advantages_section_card-title-text {
    max-width: 271px;
    width: 100%;
}
.advantages-title-section {
    /*font-size: 16px;*/
    font-weight: 400;
    margin-top: -1px;
    margin-left: -4px;
    text-transform: uppercase;
    line-height: 22px;
}
.advantages_section_card_content_item-text-pref {
    font-size: 27px;
    position: relative;
    left: -6px;
    top: -9px;
}
