.product_section {
    padding: 120px 0;
    display: grid;
    align-items: start;
    justify-content: space-between;
    grid-template-columns: repeat(2 , auto);
}
.product_section_numeric {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: 100%;
    flex-direction: column;
}
.product_section_content {
    max-width: 864px;
    width: 100%;
}
.product_section-content_title-line {
    position: relative;
}
.product_section-content_title-line:before {
    content: '';
    display: block;
    background: url("/assets/icon/productTitle-line.png") no-repeat center center;
    width: 100%;
    height: 10px;
    right: 0;
    position: absolute;
}
.product_section_content_item-title {
    display: flex;
    align-items: baseline;
}
.product_section_content_item-title-text {
    max-width: 257px;
    width: 100%;
}
.product_section_content_item_content_noCard {
    margin-top: 32px;
    display: grid;
    grid-template-columns: 348px  auto;
    align-items: start;
}
.product_section_content_item_content {
    margin-top: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product_section_content_item_content_title {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    min-height: 240px;
    margin-right: 54px;
    text-transform: uppercase;
}
.product_section_content_item_content_title > .little-text {
    line-height: 22px;
    position: relative;
    left: -4px;
}
.product_section_content_item_content_title-text {
    font-size: 68px;
    font-weight: 500;
    margin-bottom: -10px;
}
.product_section_content_item_content ul, .product_section_content_item_content_noCard ul {
    line-height: 24px;
}
