.tarif_section {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.tarif_section_content {
    max-width: 864px;
    width: 100%;
}
.tarif_section_content_item  {
    display: grid;
    grid-template-columns: max-content auto;
    align-items: center;
}
.tarif_section_content_item_title {
    display: grid;
    grid-template-columns: 271px auto;
    align-items: center;
}
.tarif_section_content_item_wrapper_item {
    max-width: 252px;
    width: 100%;
    background: var(--main_color-white);
    border-radius: 16px;
    padding: 24px 0;
}
.tarif_section_content_item_wrapper_item-title {
    margin-bottom: 8px;
}
.tarif_section_content_item_wrapper {
    display: grid;
    grid-template-columns: repeat(2, 252px);
    justify-content: space-between;
}
.tarif_section_content_item_wrapper_item-price {
    font-size: 36px;
    font-weight: 700;
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}
.tarif_section_content_item_wrapper_item-price_pref {
    font-size: 13px;
    font-weight: 400;
    color: #404B56;
}
.tarif_section_content_item_wrapper_item-price_pref-R {
    position: relative;
    top: -8px;
}
.tarif_section_content_item_wrapper_item-list {
    margin-top: 17px;
}
.tarif_section_content_item_wrapper_item-list_li {
    position: relative;
    margin-left: 48px;
    margin-top: 17px
}
.tarif_section_content_item_wrapper_item-list_li:before {
    content: '';
    position: absolute;
    display: block;
    width: 14px;
    height: 11px;
    top: 50%;
    left: -23px;
    transform: translateY(-50%);
    background: url("/assets/icon/good.png") no-repeat center center;
}
.tarif_section_content_item_wrapper_item-padding {
    padding: 0 14px;
}
.tarif_section_content_item_wrapper_hr {
    width: 100%;
    height: 1px;
    border: 1px solid #E6E7E9;
    padding: 0 10px;
}
.tarif_section_content_item_wrapper_item-list_li-gold {
    margin-left: 28px;
}
.tarif_section_content_item_wrapper_item-position {
    margin-left: 29px;
    padding: 0;
}
