.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    /*font-size: 18px;*/
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.swiper-pagination-fraction {
    right: 26px;
    width: auto;
    left: auto;
}
.swiper-button-next, .swiper-button-prev {
    right: 0;
}
