.client_section {
    padding: 120px 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.client_section_content {
    max-width: 864px;
    width: 100%;
}
.client_section_content_flex {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(6, 1fr);
    gap: 22px;
}
.client-cart {
    grid-column: span 2;
}
.client_section_content_flex > .client-cart:nth-last-child(2):first-child {

}

/* Если число равно 3, выбираем последние два элемента */
.client-cart:last-child,  .client-cart:nth-last-child(2) {
    grid-column: span 3;
}

.client_section_content_flex:last-child > .client-cart:last-child {
    margin-right: 0;
}
.client_section_content_flex-two-col {
    grid-column: span 3;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 24px;
}
.client_section_content_flex-one-col {
    grid-column: span 2;
}
